<template>
  <div class="page" id="account">
    <div class="content">
      <mt-header class="bar-nav" title="资产">
        <router-link slot="right" to="/account/message">
          <mt-button ><img src="./../../assets/images/money/asset_nav_message.png" class="account-title-img"></mt-button>
        </router-link>
      </mt-header>
      <section class="account-info">
        <span class="total-t">
          <span class="rd-text">总资产(元)</span>
          <span @click="switchFlag">
            <img v-show="flag" src="./../../assets/images/money/asset_icon_show.png" class="total-img">
            <img v-show="!flag" src="./../../assets/images/money/asset_icon_hide.png" class="total-img">
          </span>
        </span>
        <router-link to="/account/logs">
        <div class="total-n font-arial">
          <span v-show="flag">{{ resdata.totalMoney | currency('', 2)}}</span>
          <span v-show="!flag">***</span>
        </div>
        </router-link>
        <div class="profit-n">
          <span class="rd-text">累计收益(元)</span>
          <span v-show="flag" class="num font-arial">{{ resdata.earnAmount | currency('', 2)}}</span>
          <span v-show="!flag" class="num">***</span>
        </div>
        <ul class="accountInfo-list">
          <li>
            <span class="rd-text">冻结金额(元)</span>
            <p v-show="flag">{{ resdata.noUseMoney | currency('', 2)}}</p>
            <p v-show="!flag">***</p>
          </li>
          <li>
            <span class="rd-text">可用金额(元)</span>
            <p v-show="flag">{{ resdata.useMoney | currency('', 2)}}</p>
            <p v-show="!flag">***</p>
          </li>
        </ul>
      </section>
    <ul class="link-money">
      <router-link to="/account/recharge"><li><p class="aui-border-r">充值</p></li></router-link>
      <router-link to="/account/withdraw"><li>提现</li></router-link>
    </ul>
      <ul class="account-list aui-flex aui-flex-col aui-flex-middle">

        <li class="aui-flex-item-4 text-center aui-border-b aui-border-r">
          <router-link to="/account/myInvest">
          <p><img src="./../../assets/images/money/asset_icon_invest.png"></p>
          <p>我的投资</p>
          </router-link>
        </li>

        <li class="aui-flex-item-4 text-center aui-border-b aui-border-r">
          <router-link to="/account/bond/bond">
            <p><img src="./../../assets/images/money/asset_icon_transfer.png"></p>
            <p>债权转让</p>
          </router-link>
        </li>
        <li class="aui-flex-item-4 text-center aui-border-b ">
          <router-link to="/account/money_logs">
          <p><img src="./../../assets/images/money/asset_icon_realize.png"></p>
          <p>变现</p>
          </router-link>
        </li>
        <li class="aui-flex-item-4 text-center aui-border-r">
          <router-link to="/account/my_loan">
            <p><img src="./../../assets/images/money/asset_icon_loan.png"></p>
            <p>我的借款</p>
          </router-link>
        </li>
        <li class="aui-flex-item-4 text-center aui-border-r">
          <router-link to="/account/auto">
            <p><img src="./../../assets/images/money/asset_icon_reserve.png"></p>
            <p>自动投标</p>
          </router-link>
        </li>
        <li class="aui-flex-item-4 text-center">
          <router-link to="/account/my_coupon">
            <p><img src="./../../assets/images/money/asset_icon_coupon.png"></p>
            <p>我的优惠</p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import * as ajaxUrl from '../../ajax.config'
  export default {
    data(){
      return {
        resdata: '',
        flag: true
      };
    },
    created(){
      this.$indicator.open({spinnerType: 'fading-circle'}) //提示初始化加载
      let getParams = {
        userId: this.$store.state.user.userId,
        __sid: this.$store.state.user.__sid
      }
      this.$http.get(ajaxUrl.getAccountInfo, {params:getParams}).then((res) => {
        this.resdata = res.data.resData
        this.$indicator.close() // 关闭提示
      })
    },
    methods: {
      switchFlag(){
        this.flag = !this.flag
      }
    }
  }
</script>

<style scoped>
  @import "../../assets/css/account.css";
</style>



